/*-----------------------------------*\
  #PROPIEDAD PERSONALIZADA
\*-----------------------------------*/

:root {
  /**
   * colores
   */

  --verde: #329e2f;
  --dark-cornflower-blue_a7: hsla(214, 88%, 27%, 0.07);
  --white: hsla(0, 0%, 100%, 1);
  --white_a3: hsla(0, 0%, 100%, 0.03);
  --naranja: #f58f32;
  --white_traslucent: rgba(255, 255, 255, 0.205);
  --white_a70: hsla(0, 0%, 100%, 0.7);
  --naranjahover: #FF5600;
  --lavender-web: hsla(233, 52%, 94%, 1);
  --cadet-blue-crayola: hsla(220, 12%, 70%, 1);
  --cadet-blue-crayola_a20: hsla(222, 23%, 71%, 0.2);
  --azul: #0e2fa7;
  --light-gray: rgb(241, 239, 239);
  --azul-oscuro: #112056;
  --black-coral: hsla(220, 12%, 43%, 1);

  /**
   * tipografía
   */

  --ff-poppins: 'Poppins', sans-serif;

  --fs-1: calc(2.7rem + 1.38vw);
  --fs-2: calc(2.6rem + 0.66vw);
  --fs-3: 2.2rem;
  --fs-4: 1.9rem;
  --fs-5: 1.8rem;
  --fs-6: 1.7rem;
  --fs-7: 1.5rem;
  --fs-8: 1.4rem;

  --fw-700: 700;

  /**
   * espaciado
   */

  --section-padding: 90px;

  /**
   * sombra de la caja
   */

  --shadow-1: 0 10px 8px rgba(0, 0, 0, 0.25);
  --shadow-2: 0 0 0 0.05rem hsla(214, 88%, 27%, 0.08), 0 0 1.25rem hsla(216, 14%, 14%, 0.06);
  --shadow-3: 0 0 1.25rem hsla(216, 14%, 14%, 0.04);

  /**
   * radio del borde
   */

  --radius-circle: 50%;
  --radius-pill: 100px;
  --radius-10: 25px;
  --radius-8: 8px;
  --radius-6: 6px;

  /**
   * transición
   */

  --transition-1: 0.25s ease;
  --transition-2: 0.5s ease;
  --transition-3: 0.3s ease-in-out;
}

/*-----------------------------------*\
#REINICIAR
\*-----------------------------------*/

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

br {
content: "";
display: block;
font-size: 100%;
height: 1em;
}

li {
  list-style: none;
}

a,
img,
span,
input,
button,
ion-icon {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  height: auto;
}

input,
button {
  background: none;
  border: none;
  font: inherit;
}

input {
  width: 100%;
}

button {
  cursor: pointer;
}

ion-icon {
  pointer-events: none;
}

address {
  font-style: normal;
}

html {
  font-family: var(--ff-poppins);
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, #0e2fa7, #329e2f);
  background-repeat: no-repeat;
  color: white;
  font-size: 1.6rem;
  line-height: 1.7;
}

body.nav-active {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: hsl(0, 0%, 98%);
}

::-webkit-scrollbar-thumb {
  background-color: hsl(0, 0%, 80%);
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(0, 0%, 70%);
}

/*-----------------------------------*\
#ESTILO REUTILIZADO
\*-----------------------------------*/
.logo-nav{
  width: 60px;
  height: 70px;
}

.logo-footer{
  width: 60px;
  height: 70px;
}

.curso-text{
  font-size: 2rem;
}

.borderradius {
  border-radius: var(--radius-10);
}

.section-curso{
  padding-block: 50px;
  padding-bottom: 100px;
}

.objetivos li{
  list-style: disc;
  list-style-position: inside;
}

.container {
  padding-inline: 16px;
}

.section {
  padding-block: var(--section-padding);
}

.h1,
.h2,
.h3,
.h4,
.h5 {
  color: var(--white);
  font-weight: var(--fw-700);
  line-height: 1.3;
}

.h1 {
  font-size: var(--fs-1);
}

.h2 {
  font-size: var(--fs-2);
}

.h3 {
  font-size: var(--fs-3);
}

.h4 {
  font-size: var(--fs-4);
}

.h5 {
  font-size: var(--fs-6);
}

.btn {
  color: var(--white);
  font-size: var(--fs-6);
  font-weight: var(--fw-700);
  border: 2px solid var(--white);
  max-width: max-content;
  padding: 12px 28px;
  border-radius: var(--radius-pill);
  transition: var(--transition-1);
  will-change: transform;
}

.btn:is(:hover, :focus-visible) {
  transform: translateY(-4px);
}

.btn-primary,
.btn-outline:is(:hover, :focus-visible) {
  background-color: var(--white);
  color: var(--azul);
}

.img-holder {
  aspect-ratio: var(--width) / var(--height);
  background-color: var(--light-gray);
  overflow: hidden;
}

.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section-subtitle {
  font-size: var(--fs-7);
  text-transform: uppercase;
  color: var(--verde);
  font-weight: var(--fw-700);
  margin-block-end: 16px;
}

.grid-list {
  display: grid;
  gap: 25px;
}

.w-100 {
  width: 100%;
}

/*-----------------------------------*\
#HEADER
\*-----------------------------------*/

.header .btn {
  display: none;
}

.header {
  position: fixed;
  width: 100%;
  padding-block: 20px;
  box-shadow: var(--shadow-1);
  z-index: 4;
  background-color: var(--white);
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.nav-open-btn {
  font-size: 4.5rem;
  color: var(--white);
}

.header .nav-open-btn {
  color: var(--azul);
}

.navbar {
  position: fixed;
  top: 0;
  left: -300px;
  background-color: var(--verde);
  color: var(--white);
  max-width: 300px;
  width: 100%;
  height: 100vh;
  padding: 30px;
  padding-block-end: 40px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  z-index: 2;
  visibility: hidden;
  transition: var(--transition-3);
}

.navbar.active {
  visibility: visible;
  transform: translateX(300px);
}

.navbar-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-close-btn {
  background-color: var(--azul);
  color: var(--white);
  font-size: 2rem;
  padding: 6px;
  border-radius: var(--radius-circle);
}

.nav-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.navbar-list {
  margin-block-end: auto;
}

.navbar-link {
  font-weight: var(--fw-700);
  padding-block: 6px;
}

#active, .header .navbar-link:is(:hover, :focus-visible) {
  opacity: 1;
  color: var(--naranja);
}

.contact-link:is(:hover, :focus-visible) {
  color: var(--naranja);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: var(--azul);
  pointer-events: none;
  opacity: 0;
  transition: var(--transition-1);
}

.overlay.active {
  pointer-events: all;
  opacity: 0.8;
}

.vector-icon{
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(253deg) brightness(104%) contrast(101%);
}

.vector-icon:hover {
  cursor: pointer;
  filter: invert(76%) sepia(41%) saturate(3780%) hue-rotate(334deg) brightness(97%) contrast(99%);
}

.social-list, .social-list2 {
  display: flex;
  gap: 20px;
}

.social-list2 {
  align-items: center;
}

/*-----------------------------------*\
#INICIO
\*-----------------------------------*/

.home {
  padding-block-start: calc(var(--section-padding) + 70px);
  text-align: center;
}

.home .container {
  display: grid;
  gap: 30px;
}

.home-title {
  color: var(--white);
}

.home-text {
  font-size: var(--fs-5);
  color: var(--white);
  margin-block: 24px 36px;
}

.btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.home-slider,
.home-card {
  position: relative;
}

.home-card .play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--white);
  color: var(--azul);
  font-size: 2rem;
  padding: 25px;
  border-radius: var(--radius-circle);
  transition: var(--transition-1);
  animation: pulse 2s ease infinite;
}

.home-card .play-btn:is(:hover, :focus-visible) {
  color: var(--verde);
}

.home .slider-inner {
  border-radius: var(--radius-10);
  overflow: hidden;
}

.home .slider-container {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 8px;
  transition: var(--transition-2);
}

.home .slider-item {
  min-width: 100%;
  width: 100%;
  border-radius: var(--radius-10);
  overflow: hidden;
}

.home .slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--white_a70);
  color: var(--azul);
  font-size: 2rem;
  padding: 12px;
  border-radius: var(--radius-circle);
  transition: var(--transition-1);
}

.home .slider-btn:is(:hover, :focus-visible) {
  background-color: var(--white);
}

.home .slider-btn.prev {
  left: 20px;
}

.home .slider-btn.next {
  right: 20px;
}

/*-----------------------------------*\
#SERVICIO
\*-----------------------------------*/

.search-form {
  display: flex;
  gap: 15px;
  align-items: center;
  margin-bottom: 10px;
}

.search-input{
  background-color: var(--white);
  height: 60px;
  border-radius: var(--radius-6);
  box-shadow: var(--shadow-1);
  padding: 15px;
}

.search-icon{
  color: var(--white);
  font-size: 3.5rem;
  margin-top: -0.5rem;
}

.search-button{
  border: solid 3px var(--white);
  padding: 15px;
  box-shadow: var(--shadow-1);
  border-radius: var(--radius-6);
  height: 60px;
}

.search-button:hover{
  filter: brightness(0.9);
}

/*aquí no supe, ayudame, que cuando le des a un botón aparezcan los filtros*/
/* Estilos para la ventana modal */
.search-filter {
  top: 200px;
  display: block;
  position: absolute;
  background-color: #0c2850;
  border: 1px solid #ccc;
  width: 400px;
  padding: 10px;
  z-index: 1;
  border-radius: 6px;
  max-height: 507px;
  overflow-y: auto;
}



/* Estilos para la barra de desplazamiento */
.search-filter::-webkit-scrollbar {
  width: 10px; /* Ajusta el ancho de la barra de desplazamiento */
}

.search-filter::-webkit-scrollbar-track {
  background: #f1f1f1; /* Color de la pista de la barra de desplazamiento */
  border-radius: 20px; /* Asegúrate de que este valor sea igual al de .search-filter */
}

.search-filter::-webkit-scrollbar-thumb {
  background: #888; /* Color de la barra de desplazamiento */
  border-radius: 15px; /* Asegúrate de que este valor sea igual al de .search-filter */
}

.search-filter::-webkit-scrollbar-thumb:hover {
  background: #555; /* Color de la barra de desplazamiento cuando se pasa el mouse por encima */
}


#fondo-oscuro {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 0;
}



.search-button-filter{
  color: var(--white);
  border: solid 3px var(--white);
  margin-top: 10px;
  padding: 10px;
  border-radius: var(--radius-10);
}

.service {
  text-align: center;
}

.service .section-title {
  margin-block-end: 50px;
}

.service-card {
  background: var(--white_traslucent);
  padding: 40px;
  box-shadow: var(--shadow-1);
  border-radius: var(--radius-8);
}

.service-card .card-icon {
  display: grid;
  place-items: center;
  color: var(--white);
  font-size: 2.5rem;
  margin-inline: auto;
}

.service-card .card-icon img{
  border-radius: var(--radius-8);
  width: 100%;
  height: auto;
}

.service-card .card-icon ion-icon {
  --ionicon-stroke-width: 50px;
}

.service-card .card-title {
  margin-block: 16px 10px;
}

.promo-container{
  display: flex;
  justify-content: center;
}

.promo{
  color: var(--white);
  font-weight: 600;
  background: var(--naranja);
  width: 100px;
  border-radius: var(--radius-10);
  padding: 3px;
  margin-bottom: 15px;
}

.card-button {
  width: 100%;
  background: #0e2fa7;
  color: var(--white);
  font-weight: 500;
  padding: 8px;
  border-radius: var(--radius-6);
}

.card-button:hover{
  background: var(--azul-oscuro);
}

.service-card .btn-text {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin-block-start: 10px;
  color: var(--verde);
  font-weight: var(--fw-700);
  transition: var(--transition-1);
}

.service-card .btn-text:is(:hover, :focus-visible) {
  opacity: 0.9;
}

/*-----------------------------------*\
#INFORMACION
\*-----------------------------------*/

.about .container {
  display: grid;
  gap: 50px;
  margin-bottom: 100px;
  margin-top: 80px;
}

.about .section-title {
  margin-block-end: 35px;
}

.contact-button{
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white);
  font-weight: 600;
  gap: 10px;
  margin-top: 20px;
  padding: 10px 20px;
  border-radius: var(--radius-10);
  background-color: var(--naranja);
}

.contact-button:hover{
  background-color: var();
}

.contact-button img{
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(253deg) brightness(104%) contrast(101%);
}

.accordion-card .card-title {
  padding-block-end: 20px;
}

.accordion-btn {
  display: flex;
  align-items: center;
  gap: 10px;
}

.accordion-btn ion-icon {
  font-size: 1.5rem;
  color: var(--azul);
  transition: var(--transition-1);
}

.accordion-card.expanded .accordion-btn ion-icon {
  transform: rotate(0.5turn);
}

.accordion-btn .span {
  transition: var(--transition-1);
}

.accordion-btn:is(:hover, :focus-visible) .span,
.accordion-card.expanded .accordion-btn .span {
  color: var(--verde);
}

.accordion-content {
  padding-inline-start: 24px;
  max-height: 0;
  overflow: hidden;
}

.accordion-card.expanded .accordion-content {
  max-height: max-content;
  padding-block-end: 20px;
}

/*-----------------------------------*\
#FOOTER
\*-----------------------------------*/
.cta {
  background-color: var(--azul);
}

.cta .container {
  padding-block: 100px 60px;
  border-block-end: 1px solid var(--cadet-blue-crayola_a20);
}

.cta .section-title {
  color: var(--white);
  margin-block-end: 30px;
}

.cta .btn {
  background-color: var(--verde);
  color: var(--white);
  border: none;
}

.footer {
  background-color: var(--azul);
  padding-block: 60px 100px;
  color: var(--light-gray);
}

.footer-brand .footer-text {
  margin-block: 20px;
}

.footer .social-list {
  color: var(--white);
}

.fvector-icon {
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(253deg) brightness(104%) contrast(101%);
}

.fvector-icon:hover {
  cursor: pointer;
  filter: invert(76%) sepia(41%) saturate(3780%) hue-rotate(334deg) brightness(97%) contrast(99%);
}

.footer-list-title {
  color: var(--white);
  margin-block-end: 16px;
}

.footer-link {
  padding-block: 4px;
}

.footer-link:is(:hover, :focus-visible) {
  color: var(--naranja);
}

.input-wrapper {
  position: relative;
  margin-block-start: 25px;
}

.input-field {
  background-color: var(--white_a3);
  color: var(--light-gray);
  font-size: var(--fs-7);
  padding: 12px 16px;
  border: 1px solid var(--dark-cornflower-blue_a7);
  border-radius: var(--radius-6);
  box-shadow: var(--shadow-3);
  outline: none;
}

.input-field::placeholder {
  color: inherit;
}

.submit-btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 0 var(--radius-6) var(--radius-6) 0;
  background-color: var(--verde);
  color: var(--white);
  padding-inline: 24px;
  font-weight: var(--fw-700);
}

/*-----------------------------------*\
#MEDIA QUERIES
\*-----------------------------------*/

@media (max-width: 600px) {
  .container{
      padding: 0 45px;
  }
}

/* receptivo para una pantalla grande de 575 px */
@media (min-width: 575px) {
  /* ESTILO REUTILIZADO */
  .container {
      max-width: 540px;
      width: 100%;
      margin-inline: auto;
  }
}

/* receptivo para una pantalla grande de 768 px */
@media (min-width: 768px) {
  /* PROPIEDAD PERSONALIZADA */
  :root {
      /* espaciado */
      --section-padding: 120px;
  }

  /* ESTILO REUTILIZADO */
  .container {
      max-width: 720px;
  }

  .grid-list {
      grid-template-columns: 1fr 1fr;
  }

  /* ENCABEZADO */
  .header .btn {
      display: block;
      margin-inline-start: auto;
      padding: 8px 20px;
  }

  .header.active .btn {
      background-color: var(--verde);
      border-color: var(--verde);
      color: var(--white);
  }

  /* INICIO */
  .home-content {
      max-width: 85%;
      margin-inline: auto;
  }

  .home-text {
      --fs-5: 2rem;
  }

  /* SERVICIOS */
  .service .section-title {
      max-width: 30ch;
      margin-inline: auto;
  }

  /* INFORMACION */
  .about .container {
      grid-template-columns: 1fr 0.7fr;
      align-items: center;
  }
}

@media (max-width: 991px) {
  .social-list2{
      display: none;
  }
}

/* receptivo para una pantalla grande de 992px */
@media (min-width: 992px) {
  /* ESTILO REUTILIZADO */
  .container {
      max-width: 960px;
  }

  /* ENCABEZADO */
  .nav-open-btn,
  .navbar > *:not(.navbar-list, .social-list2),
  .overlay {
      display: none;
  }

  .navbar{
      all: unset;
      display: flex;
      width: 100%;
  }

  .social-list2 {
      margin-left: auto;
  }

  .navbar-list {
      display: flex;
      gap: 30px;
      margin: auto;
  }

  .header .btn {
      margin-inline-start: 0;
  }

  .navbar-link{
      color: var(--verde);
  }

  .vector-icon {
      filter: invert(54%) sepia(44%) saturate(783%) hue-rotate(70deg) brightness(84%) contrast(88%);
  }

  /* INICIO */
  .home {
      padding-block-start: calc(var(--section-padding) + 90px);
      text-align: left;
  }

  .home .container {
      grid-template-columns: 1fr 0.8fr;
      align-items: center;
  }

  .home-content {
      max-width: unset;
      margin-inline: 0;
  }

  .btn-wrapper {
      justify-content: flex-start;
  }

  /* INVITACION */
  .cta .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }

  .cta .btn {
      min-width: max-content;
  }

  .cta .section-title {
      max-width: 30ch;
      margin-block-end: 0;
  }

  /* FOOTER */
  .footer .grid-list {
      grid-template-columns: repeat(4, 1fr);
  }
}

/* receptivo para una pantalla grande de 1200 px */
@media (min-width: 1200px) {
  /* ESTILO REUTILIZADO */
  .container {
      max-width: 1140px;
  }

  /* INICIO */
  .home-text {
      padding-inline-end: 100px;
  }

  /* SERVICIO */
  .service .grid-list {
      grid-template-columns: repeat(4, 1fr);
  }

  /* FOOTER */
  address.footer-text {
      padding-inline-end: 100px;
      margin-block-end: 16px;
  }
}

/* receptivo para una pantalla grande de 1400px */
@media (min-width: 1400px) {
  /* ESTILO REUSABLE */
  .container {
      max-width: 1320px;
  }
}

